import React from 'react';
import {Redirect} from "react-router-dom";
import {connect} from "react-redux";

class Home extends React.Component {
  state = {
    age: '',
    name: 'hai',
    toDashboard: false,
  };

  handleChange = event => {
    this.setState(() => ({
      toDashboard: true
    }));
    this.setState({ [event.target.name]: event.target.value });
    console.log(event.target.value)
    this.props.appSelected(event.target.value);
  };

  render() {
    const { classes } = this.props;

    if (this.state.toDashboard === true) {
      return <Redirect to='/play'/>
    }

    return (
      <select value={this.state.age} onChange={this.handleChange}>
        <option value={'Bomberman'}>Bomberman</option>
        <option value={'DigDug1'}>DigDug1</option>
        <option value={'DigDug2'}>DigDug2</option>
      </select>
    );
  }
}

function mapDispatchToProps(dispatch) {
  return {
    appSelected: (appName) => dispatch({type: 'APP_SELECTED', value: appName})
  };
}

export default connect(null, mapDispatchToProps)(Home);


